.hero {
  background-color: #000;
  height: 100vh;
}
.hero__swipe {
  pointer-events: none;
  display: none;
  margin: 0 auto;
  animation: bobbing 3s ease-in-out infinite;
}
.hero__text {
  margin: 0;
}
.hero__content {
  display: flex;
  height: 100%;
  font-size: 30px;
}
.hero__content h1 {
  position: absolute;
  width: 1px;
  height: 1px;
  padding: 0;
  margin: -1px;
  overflow: hidden;
  clip: rect(0, 0, 0, 0);
  white-space: nowrap;
  border: 0;
}
.hero__bg {
  width: 100px;
  height: 100%;
  position: fixed;
  z-index: 99;
  bottom: 0;
  left: -33px;
  overflow: hidden;
  background-image: url("../assets/images/bg-main.png");
  background-size: contain;
  transform: scale(-1, -1);
}
.hero__bg--mobile {
  display: none;
}
.hero__social {
  display: flex;
  width: 100%;
  gap: 30px;
  justify-content: center;
}
.hero__social svg {
  width: 30px;
  height: 30px;
}
.hero__social-link {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 40px;
  height: 40px;
  border-radius: 50%;
  padding: 4px;
  color: #fff;
}
.hero__social-link::before {
  content: "";
  display: block;
  position: absolute;
  top: -5px;
  left: -5px;
  width: 120%;
  height: 120%;
  background-color: transparent;
  mask-image: url("../assets/images/mask.png");
  mask-size: contain;
  mask-repeat: no-repeat;
  mask-position: center;
  transform: rotate(90deg) scale(0);
  -webkit-mask-image: url("../assets/images/mask.png");
  -webkit-mask-size: cover;
  -webkit-mask-repeat: no-repeat;
  -webkit-mask-position: center;
  transition: transform 0.5s, background-color 0.5s;
}
.hero__social-link svg {
  position: relative;
  z-index: 1;
  fill: currentColor;
}
.hero__info {
  position: relative;
  display: flex;
  gap: 3rem;
  flex-direction: column;
  justify-content: center;
  width: 50%;
  padding: 2em 1em;
  text-align: center;
  overflow: hidden;
  min-height: 100vh;
}
.hero__avatar {
  margin: 0 auto;
  width: 40%;
  aspect-ratio: 1;
  flex-shrink: 0;
  border-radius: 50%;
  overflow: hidden;
  position: relative;
}
.hero__avatar img {
  position: absolute;
  inset: 0;
  height: 110%;
  width: 110%;
  object-fit: cover;
  object-position: center center;
}
.hero__preview {
  width: 50%;
  aspect-ratio: 1;
  min-height: 100vh;
  overflow: hidden;
  position: relative;
}
.hero__preview canvas, .hero__preview img {
  position: absolute;
  inset: 0;
  height: 100%;
  width: 100%;
  object-fit: cover;
  object-position: center center;
  cursor: pointer;
  will-change: transform, contents;
  transform: translateZ(0);
}
.hero__preview.is-ready canvas {
  opacity: 1;
}
.hero__preview.is-ready img {
  opacity: 0;
}

@media (hover: hover) {
  .hero__social-link:hover::before {
    background-color: #fff;
    transform: none;
  }
  .hero__social-link--tg:hover {
    color: rgb(51, 144, 236);
  }
  .hero__social-link--vk:hover {
    color: #07f;
  }
  .hero__social-link--phone:hover {
    color: #0ece00;
  }
  .hero__social-link--inst:hover {
    color: rgb(226, 31, 177);
  }
  .hero__social-link--ym:hover {
    color: #f43;
  }
}
@media only screen and (max-width: 1199px) {
  .hero__bg {
    left: -62px;
  }
  .hero__content {
    font-size: 24px;
  }
}
@media only screen and (max-width: 1023px) {
  .hero {
    height: auto;
  }
  .hero__avatar {
    width: 50%;
  }
  .hero__swipe {
    display: flex;
  }
  .hero__bg {
    position: absolute;
    height: 60px;
    left: 0;
    top: -42px;
    width: 100%;
    background-image: url("../assets/images/bg-mobile.png");
    transform: none;
  }
  .hero__bg--mobile {
    display: flex;
    top: auto;
    transform: scale(-1, -1);
  }
  .hero__content {
    flex-direction: column;
  }
  .hero__preview {
    display: none;
  }
  .hero__info {
    width: 100%;
    gap: 2rem;
  }
}
@keyframes bobbing {
  0%, 100% {
    transform: translateY(0); /* Start and end at the original vertical position */
  }
  50% {
    transform: translateY(-10px); /* Move 10 pixels up at the midpoint of the animation */
  }
}