.preloader {
  position: fixed;
  z-index: 99;
  inset: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #fff;
  transition: opacity 0.6s;
  user-select: none;
  animation: pulse-animation 3s ease-in-out infinite;
}
.preloader img {
  width: 300px;
  height: 300px;
}
.preloader.is-loaded {
  opacity: 0;
  pointer-events: none;
}

@keyframes pulse-animation {
  0% {
    transform: scale(1);
  }
  50% {
    transform: scale(1.03);
  }
  100% {
    transform: scale(1);
  }
}